.move {
    animation-name : mymove;
    animation-iteration-count : infinite;
    animation-direction : alternate;
    animation-duration :   1s;
  }
  @keyframes mymove {
    from {transform: translateY(0px);}
    to {transform: translateY(10px);}
  }
  @media (max-width: 767px) {
    .img {
      display: none;
    }
  }